<script>
    import { onMount } from 'svelte';

    let isOpen = false;
    let isSearchOpen = false;

    function toggleMenu() {
        isOpen = !isOpen;
    }

    function handleKey(event) {
        if (event.key === 'Enter' || event.key === ' ') {
            toggleMenu();
        }
    }

    function toggleSearch() {
        isSearchOpen = !isSearchOpen;
        const searchInput = document.querySelector('.search input');
        if (isSearchOpen) {
            searchInput.style.display = 'block';
            searchInput.focus();
        } else {
            searchInput.style.display = 'none';
        }
    }

    function handleClickOutside(event) {
        const searchInput = document.querySelector('.search input');
        const searchButton = document.querySelector('.search button');

        if (isSearchOpen && !searchInput.contains(event.target) && !searchButton.contains(event.target)) {
            isSearchOpen = false;
            searchInput.style.display = 'none';
        }
    }

    onMount(() => {
        window.addEventListener('click', handleClickOutside);
        return () => {
            window.removeEventListener('click', handleClickOutside);
        };
    });
</script>


<main>
    <nav class="navbar">
        <div class="nav-logo"><a href="/">WebLab</a></div>
        <ul class="nav-menu {isOpen ? 'open' : ''}">
            <li><a href="/mathpage">수학</a></li>
            <li><a href="/sciencepage">자연 과학</a></li>
            <li><a href="/cspage">컴퓨터 과학</a></li>
            <li class="search">
                <input type="text" placeholder="   검색어를 입력하세요..." style="display: {isSearchOpen ? 'block' : 'none'};" aria-label="Search">
                <button on:click={toggleSearch} on:keydown={toggleSearch} aria-label="Toggle search">
                    <img src="./images/search.png" alt="search">
                </button>
            </li>
        </ul>
        <button class="hamburger" on:click={toggleMenu} on:keydown={handleKey} aria-label="Toggle menu">
            <div></div>
            <div></div>
            <div></div>
        </button>
    </nav>
</main>

<style>
    .navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
        background-color: #333;
        color: white;
    }

    .nav-logo a {
        color: white;
        text-decoration: none;
    }

    .nav-menu {
        display: flex;
        list-style: none;
    }

    .nav-menu li {
        margin: 0 10px;
    }

    .nav-menu li a {
        color: white;
        text-decoration: none;
    }

    .search {
        display: flex;
        align-items: center;
        position: relative;
    }

    .search input {
        display: none;
        position: absolute;
        right: 30px;
        padding: 5px;
        border: none;
        border-radius: 5px;
    }

    .search button {
        background: none;
        border: none;
        cursor: pointer;
    }

    .search img {
        width: 20px;
        height: 20px;
        filter: invert(1);
        vertical-align: middle;
    }

    .hamburger {
        display: none;
        flex-direction: column;
        justify-content: space-around;
        width: 30px;
        height: 30px;
        background: transparent;
        border: none;
        cursor: pointer;
    }

    .hamburger div {
        width: 30px;
        height: 3px;
        background: white;
    }

    @media (max-width: 768px) {
        .nav-menu {
            display: none;
            flex-direction: column;
            width: 100%;
            text-align: center;
            background-color: #333;
        }

        .nav-menu.open {
            display: flex;
        }

        .hamburger {
            display: flex;
        }

        .search input {
            display: block;
            position: static;
        }
    }
</style>
