<script>
  import { onMount } from "svelte";
  import * as THREE from "three";

  let container;

  onMount(() => {
    let scene, camera, renderer, center_sphere, corner_spheres_group;

    function init() {
      scene = new THREE.Scene();

      camera = new THREE.PerspectiveCamera(75, container.clientWidth / container.clientHeight, 0.1, 1000);
      camera.position.z = 15;

      renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
      renderer.setSize(container.clientWidth, container.clientHeight);
      renderer.setClearColor(0x000000, 0);
      container.appendChild(renderer.domElement);

      // Central sphere
      const center_geometry = new THREE.SphereGeometry(2, 32, 32);
      const center_material = new THREE.MeshBasicMaterial({ color: 0x44aa88, wireframe: true });
      center_sphere = new THREE.Mesh(center_geometry, center_material);
      scene.add(center_sphere);

      // Corner spheres
      corner_spheres_group = new THREE.Group();
      const corner_geometry = new THREE.SphereGeometry(0.5, 32, 32);
      const corner_material = new THREE.MeshBasicMaterial({ color: 0x44aa88, wireframe: true });
      const distance = 4;
      const positions = [
        [distance, distance, distance],
        [distance, distance, -distance],
        [distance, -distance, distance],
        [distance, -distance, -distance],
        [-distance, distance, distance],
        [-distance, distance, -distance],
        [-distance, -distance, distance],
        [-distance, -distance, -distance],
      ];

      positions.forEach(pos => {
        const sphere = new THREE.Mesh(corner_geometry, corner_material);
        sphere.position.set(pos[0], pos[1], pos[2]);
        corner_spheres_group.add(sphere);
      });

      scene.add(corner_spheres_group);

      function animate() {
        requestAnimationFrame(animate);

        // Rotate the central sphere
        center_sphere.rotation.y += 0.005;
        center_sphere.rotation.x += 0.005;

        // Rotate the group of corner spheres around the center
        corner_spheres_group.rotation.y += 0.01;
        corner_spheres_group.rotation.x += 0.01;

        renderer.render(scene, camera);
      }

      animate();
    }

    init();

    window.addEventListener("resize", () => {
      camera.aspect = container.clientWidth / container.clientHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(container.clientWidth, container.clientHeight);
    });
  });
</script>

<style>
  .container {
    padding: 100px;
    height: 100vh;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-top: 0px;
  }

  :global(.container canvas) {
    width: 100% !important;
    height: 100% !important;
  }
</style>

<div bind:this={container} class="container"></div>