<script>
    import { onMount } from 'svelte';
    import Markdown from 'svelte-markdown';
    import allContent from "../data/content.json";

    export let id;

    let contentItem = null;
    let markdownContent = "";
    let errorMessage = "";

    onMount(async () => {
        contentItem = allContent.find(item => item.id === id);

        if (contentItem && contentItem.mdFile) {
            try {
                // Use fetch to load the markdown file
                const response = await fetch(`/Contents/${contentItem.mdFile}`);
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                markdownContent = await response.text();
            } catch (error) {
                console.error("Error loading markdown file:", error);
                errorMessage = "콘텐츠를 불러오는 데 실패했습니다.";
            }
        } else {
            errorMessage = "콘텐츠를 찾을 수 없습니다.";
        }
    });
</script>

<main>
    {#if contentItem}
        <h1>{contentItem.title}</h1>
        <p class="tags">{contentItem.tags.join(' ')}</p>
        <p class="date">{contentItem.date}</p>
        <div class="markdown-body">
            <Markdown source={markdownContent} />
        </div>
    {:else if errorMessage}
        <p class="error-message">{errorMessage}</p>
    {:else}
        <p>로딩 중...</p>
    {/if}
</main>

<style>
    main {
        padding: 20px;
        max-width: 800px;
        margin: 0 auto;
    }

    h1 {
        margin-top: 20px;
        margin-bottom: 10px;
        font-size: 2.5rem;
    }

    .tags {
        color: #666;
        font-size: 0.9rem;
        margin-bottom: 5px;
    }

    .date {
        color: #999;
        font-size: 0.8rem;
        margin-bottom: 20px;
    }

    .markdown-body {
        line-height: 1.6;
    }

    .error-message {
        color: red;
        text-align: center;
        margin-top: 50px;
    }
</style>
