
<script>
  import { onMount } from 'svelte';
  import Swiper from 'swiper';
  import 'swiper/swiper-bundle.css';
  import { navigate } from "svelte-routing"; // Import navigate function

  export let title;
  export let contentItems = [];

  let swiperContainer;

  onMount(() => {
    const swiper = new Swiper(swiperContainer, {
      // Optional parameters
      loop: true,
      slidesPerView: 3, // Changed back to 3
      spaceBetween: 30,
      autoHeight: true, // Added to allow container to adjust height

      // If we need pagination
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },

      // Navigation arrows
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    });
  });

  function goToContentDetail(id) {
    navigate(`/content/${id}`);
  }
</script>

<div class="carousel-section">
  <h2>{title}</h2>
  <div class="swiper-container" bind:this={swiperContainer}>
    <div class="swiper-wrapper">
      {#each contentItems as item (item.id)}
        <div class="swiper-slide" on:click={() => goToContentDetail(item.id)} style="cursor: pointer;">
          <h3>{item.title}</h3>
          <p class="tags">{item.tags.join(' ')}</p>
          <!-- <p class="description">{item.description}</p> --> <!-- Removed description -->
        </div>
      {/each}
      {#if contentItems.length === 0}
        <div class="swiper-slide">콘텐츠 없음</div>
      {/if}
    </div>
    <!-- Add Pagination -->
    <div class="swiper-pagination"></div>

    <!-- Add Navigation -->
    <div class="swiper-button-prev"></div>
    <div class="swiper-button-next"></div>
  </div>
</div>

<style>
  .carousel-section {
    padding: 40px 20px;
    background-color: #fff;
  }

  h2 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 2.5rem;
  }

  .swiper-container {
    width: 100%;
    /* height: 300px; */ /* Removed fixed height */
    overflow: hidden;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #eee;

    /* Center slide text vertically */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    padding: 20px;
  }

  .swiper-slide h3 {
    margin-bottom: 10px;
    font-size: 1.5rem;
  }

  .swiper-slide .tags {
    font-size: 0.9rem;
    color: #666;
  }

  .swiper-slide .description {
    font-size: 1rem;
    color: #333;
    margin-top: 10px;
  }
</style>
